body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header {
  display: flex;
  width: 600px;
  margin: 15px auto;
}

.wrapper {
  max-width: 900px;
  margin: 0 auto;
  font-family: sans-serif;
}

.logo img {
  width: 180px;
  height: 40px;
  margin: 0;
}

img {
  width: 100%;
  margin: 6px 0;
}

a {
  color: #004d99;
  font-weight: bold;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.news-description {
  font-size: 12px;
  margin: 6px 0;
}

.content-wrapper {
  margin-top: 5px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  grid-template-rows: repeat(2, 1fr);
  grid-gap: 1rem;
  grid-column-start: 1;
  grid-column-end: 1;
  grid-row-start: 1;
  grid-row-end: 1;
  justify-items: center;
}

.news-wrapper {
  width: 400px;
}

.logo div {
  font-family: sans-serif;
  font-weight: 700;
  color: #e70000;
  -webkit-transform: rotate(-40deg);
          transform: rotate(-40deg);
  width: 70px;
  height: 20px;
  top: 0;
  font-size: 26px;
  text-shadow: 2px 4px 3px rgba(0, 0, 0, 0.3);
  margin-top: -35px;
}

.navigate {
  width: 450px;
  height: 40px;
  background-color: #ffe80e;
  display: flex;
  color: black;
  font-family: sans-serif;
  font-weight: 700;
  justify-content: space-around;
  font-size: 12px;
  margin-left: 20px;
}

.navigate-items {
  height: 40px;
  line-height: 40px;
  padding: 0 5px;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.active {
  border-bottom: 2px solid black;
}

.App-header {
  margin-top: 10px;
}

#preloader {
  position: absolute;
  top: 50%;
  left: 50%;
  right: 50%;
  margin-left: -100px;
  height:200px;
  width:200px;
}
.sort-panel{
  display: flex;
  justify-content: space-evenly;
  background-color: #ffe80e;
  padding: 10px 0;
}

button{
  margin: 3px;
}
